export default {
	data() {
		return {
			eyeImg1: false,
			popo:false,
			li:'',
			tid:'',
			jid:'',
			ajid:'',
			order_id:'',
			addressId:'',
			massagist_id:'',
			date:'',
			begin:'',
			end:'',
			free:'',
			can:'',
			aa:'',
			times:'',
			remark:'',
			lidetail:'',
			remarks:[],
			tagg:[
				{value:'可提前来'},
				{value:'准时服务'},
				{value:'来前联系'}
			],
		};
	},
	computed:{
		labels(){
			return this.remarks.join('，')
		}
	}, 
	onLoad(e){
		this.authKey = uni.getStorageSync('authKey');
		this.biId = e.id;
		this.getDress();
		this.getcan();
	},
	onShow(){
		let jishi = uni.getStorageSync('jishi')
		let date = uni.getStorageSync('date');
		let begin = uni.getStorageSync('begin');
		let end = uni.getStorageSync('end');
		let value = uni.getStorageSync('value');
			
		if (jishi){
			this.li = jishi
		} else {
			this.teach();
		}
		
		if (value){
			this.date = date
			this.begin = begin
			this.end = end
			this.times = value
		}
	},
	methods:{
		// 技师
		teach(){
			if(this.lidetail == ''){
				uni.request({
					url: this.http+'home/myMassagists',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
					},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							let li = res.data.data[0]
							li.avatarurl = this.static + li.avatarurl
							this.li = li										
						}
					},
				});
			}
		},
		// 到店地址
		getDress(){
			uni.request({
				url: this.http+'appointment/getClubAddress',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
				},
				success: res => {
					console.log(res)
					if (res.data.code == 200) {
						this.aa = res.data.data
					}
				},
			});
		},
		// 是否可选技师
		getcan(){
			uni.request({
				url: this.http+'appointment/canichoose',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						this.can = res.data.data
					}
				},
			});
		},
		remarkInput: function(e) {
			this.remark = e.target.value;
		},
		tag(e){
			if (this.remark.indexOf(e.value) < 0)
				this.remarks.push(e.value)
				this.remark += e.value;
		},
		// 立即预约
		tiji(){
			var that= this
			this.date = this.date.replace("-","")
			uni.request({
				url: this.http+'appointment/create',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {
					mode:1,
					order_id:this.biId,
					address_id:'',
					massagist_id:this.li.id,
					date:this.date,
					begin:this.begin,
					end:this.end,
					remark:this.remark,
				},
				success: res => {
					console.log(res.data)
					var datal = res.data.data
					this.order_id = datal.order_id
					if (res.data.code == 200) {
						this.popo = true
					}else{
						uni.showToast({
							title:res.data.error,
							icon:'none',
							duration:1500
						})
					}
				},
			});
		},
		close(){
			this.popo = false
		},
		godetail(){
			 uni.navigateTo({
				url: '/pages/appointmentDetail/appointmentDetail?id=' + this.order_id
			});
		},
		gotimes(){
			 uni.navigateTo({
				url: '/pages/choose/times?id=' + this.biId +'&jid=' + this.li.id + '&sid=2'
			});
		},
		gotech(){
			uni.navigateTo({
				url: '/pages/choose/technicians?id=' + this.biId +'&jid=' + this.li.id + '&sid=2'
			});
		}
	}
}
